const ReactDOM = {
  render,
}

function render(vnode, container) {
  console.log('vnode.........', vnode);
  if (vnode === undefined) return;

  // Â¶ÇÊûúvnodeÊòØÂ≠óÁ¨¶‰∏≤
  if (typeof vnode === 'string') {
    // ÂàõÂª∫ÊñáÊú¨ËäÇÁÇπ
    const textNode = document.createTextNode(vnode);
    return container.appendChild(textNode);
  }


  // Âê¶ÂàôÂ∞±ÊòØËôöÊãüDOMÂØπË±°
  const {
    tag,
    attrs,
    childrens = [],
  } = vnode;
  // ÂàõÂª∫ËäÇÁÇπÂØπË±°
  const dom = document.createElement(tag);
  if (attrs) {
    // ÊúâÂ±ûÊÄß
    Object.keys(attrs).forEach(key => {
      const value = attrs[key];
      setAttribute(dom, key, value);
    });
  }

  // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
  childrens.forEach(node => render(node, dom));

  return container.appendChild(dom);

  // ËÆæÁΩÆÂ±ûÊÄß
  function setAttribute(dom, key, value) {
    //Â∞ÜÂ±ûÊÄßÂêçclassNameËΩ¨Êç¢Êàêclass
    if (key === 'className') {
      key = 'class';
    }
    // Â¶ÇÊûúÊòØ‰∫ã‰ª∂ onClick onBlur ...
    if (/on\w+/.test(key)) {
      //ËΩ¨Â∞èÂÜô
      key = key.toLowerCase();
      dom[key] = value || '';
    } else if (key === 'style') { // ÈíàÂØπÂ±ûÊÄß‰∏∫styleÁöÑÂ§ÑÁêÜ
      if (!value || typeof value === 'string') {
        dom.style.cssText = value || '';
      } else if (value && typeof value === 'object') {
        for (let k in value) {
          // {width: 20}
          if (typeof value[k] === 'number') {
            dom.style[k] = value[k] + 'px';
          } else {
            dom.style[k] = value[k];
          }
        }
      }
    } else {
      // console.log('dom................', dom);
      // console.log('key................', key);
      // console.log('key in dom................', key in dom);
      // ÂÖ∂‰ªñÂ±ûÊÄß
      if (key in dom) {
        dom[key] = value || ''
      }
      if (value) {
        // Êõ¥Êñ∞ÂÄº
        dom.setAttribute(key, value);
      } else {
        dom.removeAttribute(key);
      }
    }

  }

}

export default ReactDOM;